"use client";

import { useEffect, useState, useRef } from "react";
import { useParams, useRouter } from "next/navigation";
import { useAuth } from "@/contexts/AuthContext";
import AppLayout from "@/components/AppLayout";
import { useSocket } from "@/contexts/SocketContext";
import api from "@/services/api";

export default function ChatPage() {
  const { user, loading } = useAuth();
  const { userId } = useParams();
  const router = useRouter();
  const socket = useSocket();

  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const typingTimerRef = useRef(null);
  const messagesEndRef = useRef(null);

  // Redirect if not authed
  useEffect(() => {
    if (!loading && !user) router.push("/login");
  }, [loading, user, router]);

  // Once socket & user are ready, join + load history + bind listeners
  useEffect(() => {
    if (!socket || !user) return;

    socket.emit("join", user.id);

    (async () => {
      try {
        const { data } = await api.get(`/messages/${userId}`);
        setMessages(data);
      } catch (err) {
        console.error("Failed to load history:", err);
      }
    })();

    socket.on("receive_message", (msg) =>
      setMessages((prev) => [...prev, msg])
    );
    socket.on("message_sent", (msg) => setMessages((prev) => [...prev, msg]));
    socket.on("typing", ({ from }) => {
      if (from === userId) setIsTyping(true);
    });
    socket.on("stop_typing", ({ from }) => {
      if (from === userId) setIsTyping(false);
    });

    return () => {
      socket.off("receive_message");
      socket.off("message_sent");
      socket.off("typing");
      socket.off("stop_typing");
    };
  }, [socket, user, userId]);

  // Scroll on new message
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  }, [messages]);

  const send = () => {
    if (!socket || !input.trim()) return;
    socket.emit("send_message", {
      receiver_id: userId,
      content: input,
    });
    setInput("");
    socket.emit("stop_typing", { to: userId });
  };

  const onType = (e) => {
    setInput(e.target.value);
    if (!socket) return;
    socket.emit("typing", { to: userId });
    clearTimeout(typingTimerRef.current);
    typingTimerRef.current = setTimeout(() => {
      if (socket) socket.emit("stop_typing", { to: userId });
    }, 800);
  };

  // Show loading states
  if (loading || !user) return <p>Loading user…</p>;
  if (!socket) return <p>Connecting to chat…</p>;

  return (
    <AppLayout>
      <div className="flex flex-col max-w-md mx-auto mt-8 h-[80vh]">
        <div className="flex-1 overflow-y-auto px-2">
          {messages.map((msg, i) => (
            <div
              key={i}
              className={`my-2 flex ${
                msg.sender_id === user.id ? "justify-end" : "justify-start"
              }`}
            >
              <div
                className={`px-4 py-2 rounded-lg ${
                  msg.sender_id === user.id
                    ? "bg-blue-500 text-white"
                    : "bg-gray-700 text-white"
                }`}
              >
                {msg.content}
              </div>
            </div>
          ))}
          <div ref={messagesEndRef} />
        </div>

        {isTyping && <p className="italic text-sm text-gray-400">Typing…</p>}

        <div className="mt-2 flex gap-2">
          <input
            type="text"
            value={input}
            onChange={onType}
            onKeyDown={(e) => e.key === "Enter" && send()}
            className="flex-1 px-4 py-2 rounded bg-gray-800"
            placeholder="Type a message…"
            disabled={!socket}
          />
          <button
            onClick={send}
            disabled={!socket || !input.trim()}
            className={`px-4 py-2 rounded transition ${
              !socket || !input.trim()
                ? "bg-gray-600 cursor-not-allowed"
                : "bg-blue-600 hover:bg-blue-700"
            }`}
          >
            Send
          </button>
        </div>
      </div>
    </AppLayout>
  );
}
